import React from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import classNames from 'classnames';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

const Footer = props => {
	return (
		<footer className={classNames('footer row p-6 bg-gray-700', props.className)}>
			<div className='col'>
				<ul>
					<li>
						<a href='/'>Home</a>
					</li>
					{process.env.NODE_ENV === 'dev' && (
						<li>
							<a href='/dev'>Sandbox</a>
						</li>
					)}
				</ul>
			</div>
			<div className='col'>
				<ul>
					<li>
						<FooterLink
							to='https://progameguides.com/wp-content/uploads/2019/06/tft-full-item-cheat-sheet-desktop-set2-9-24b.png'
							external
						>
							Full Cheat Sheet (9.24)
						</FooterLink>
					</li>
					<li>
						<FooterLink
							to='https://progameguides.com/wp-content/uploads/2019/06/tft-full-item-cheat-sheet-set2-9-24b.png'
							external
						>
							Items Cheat Sheet (9.24)
						</FooterLink>
					</li>
				</ul>
			</div>
		</footer>
	);
};
const mapStateToProps = state => ({});
const mapDispatchToProps = dispatch => bindActionCreators({}, dispatch);
export default connect(mapStateToProps, mapDispatchToProps)(Footer);

export const FooterLink = props => {
	return (
		<a href={props.to} target={props.external ? '_blank' : null} rel={props.external ? 'noopener noreferrer' : null}>
			{props.children}
			{props.external && <FontAwesomeIcon icon='external-link-alt' className='ml-3 ext-link-icon' />}
		</a>
	);
};
